.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
   "h h"
   "n c";
    grid-template-rows:  60px 1fr;
    grid-template-columns: 2fr 10fr;

}


.app-wrapper-content {
    border-radius: 5px;
    margin-top: 5px;
    background-color: 	#f5f5f5;
    grid-area: c;

}


.app-wrapper-content img {
    border-radius: 5px;
    width: 1000px;
    height: 200px;
    image-rendering: pixelated;
}
